.imgCont {
  width: 200px;
  height: 200px;
  overflow: hidden;
  position: relative;
  margin: 30px auto;
}

.img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
}

.desc {
  margin: 30px;
  font-weight: 500;
  font-size: 16px;
}