@import '../../styles/varaibles';

.fieldBlock {
  position: relative;
  width: 100%;
  margin-bottom: 19px;
}

.field {
  label {
    font-size: 18px;
    line-height: 20px;
    color: $textColor;
    opacity: 0.5;
  }

  input {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
  }
}

.butCont {
  width: 52px;
  position: absolute;
  right: 24px;
  top: 50%;
  transform: translateY(-50%);
}

.spanBlock {
  display: flex;
  align-items: center;
  margin-bottom: 22px;
}

.span {
  margin-right: 8px;
  font-weight: 500;
  color: $textColor;
  font-size: 20px;
  line-height: 24px;
}
